import React, { Suspense } from "react";

import Api from "./api";
import "./styles/frame.css";

const Card = React.lazy(() => import("./card"));

export default function Frame(props) {
  const data = [...Api].sort((a, b) => {
    if (props.orden === "A") {
      return a.name.localeCompare(b.name);
    } else if (props.orden === "B") {
      return b.name.localeCompare(a.name);
    } else {
      return Api;
    }
  });
  const dato = [...data].filter((i) => {
    if (props.occupation === "dirección ejecutiva") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "administración y finanzas") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "comunicaciones") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "planificación y desarrollo") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "relaciones internacionales") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "investigación y e.m") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "escuela nacional de migración") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "transparencia") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "recursos humanos") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "tecnología") {
      return i.section.toLowerCase().includes(props.occupation);
    } else if (props.occupation === "cocina") {
      return i.section.toLowerCase().includes(props.occupation);
    } else {
      return i;
    }
  });

  const filtrar = dato.filter((info) => {
    if (props.input === "") {
      return info;
    } else {
      return info.name.toLowerCase().includes(props.input);
    }
  });

  return (
    <div className="Frame">
      <Suspense fallback={<>Loading...</>}>
        {filtrar.map((item, index) => {
          return (
            <div key={index}>
              <Card
                img={item.img}
                name={item.name}
                phone={item.phone}
                section={item.section}
                email={item.email}
                whatsapp={item.whatsapp}
                position={item.ocupation}
                direct={item.direct}
              />
            </div>
          );
        })}{" "}
        <>
          {filtrar.length === 0 && (
            <h1 className="Error" title="Prueba otro nombre">
              No localizado
            </h1>
          )}
        </>
      </Suspense>
    </div>
  );
}
